@import '../../../../includes/assets/css/consts.scss';

.wpuxss-tpb {

	a.wpuxss-tpb-button {
		transition: 0.3s color ease-in-out !important;
	}

	.dpit-admin-btn__bullets {
		position: absolute !important;
		display: flex !important;
		transition: all 0.5s ease-in-out !important;

		ul {
			margin: 13px auto 1px auto !important;
			padding: 0 !important;

			li {
				list-style: none !important;
				margin: 0 1px 5px 1px !important;
				width: 5px !important;
				height: 5px !important;

				background-color: rgba(var(--dpu-color-3-text), 0.336) !important;
				border-radius: 50% !important;
				transition: transform $transition-time-fast, background-color $transition-time-fast !important;

				&.active {
					background-color: rgba(var(--dpu-color-3-text), 1) !important;
					transform: translateY(-100%) !important;
				}

				&.semi-active {
					transform: translateY(-50%) !important;
				}
			}
		}
	}
}